import React, { useState, useEffect } from 'react';

import Box from '@mui/material/Box';
import Table from '@mui/material/Table';
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import Switch from '@mui/material/Switch';
import Tooltip from '@mui/material/Tooltip';
import TableRow from '@mui/material/TableRow';
import TableCell from '@mui/material/TableCell';
import TableBody from '@mui/material/TableBody';
import TableHead from '@mui/material/TableHead';
import EditIcon from '@mui/icons-material/Edit';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import DeleteIcon from '@mui/icons-material/Delete';
import TableContainer from '@mui/material/TableContainer';

import PaginationBox from '../../../common/PaginationBox';

const BannerList = ({
  response,
  handleDelete,
  addNewBanner,
  handleEditBanner,
  handleStatusToggle,
}) => {
  const [rows, setRows] = useState([]);
  const [paginationData, setPaginationData] = useState({});

  useEffect(() => {
    if (response && response.status) {
      setRows(
        response.data.data
          .filter((each) => each.role !== 1)
          .map((each) => {
            const { id, title, description, link, type, status, banner_image } =
              each;
            const eachRow = {
              id,
              link,
              type,
              title,
              status,
              description,
              banner_image,
            };
            return eachRow;
          })
      );
      const {
        current_page,
        first_page_url,
        from,
        last_page,
        last_page_url,
        next_page_url,
        per_page,
        prev_page_url,
        to,
        total,
      } = response.data;
      setPaginationData({
        current_page,
        first_page_url,
        from,
        last_page,
        last_page_url,
        next_page_url,
        per_page,
        prev_page_url,
        to,
        total,
      });
    }
  }, [response]);

  if (!rows.length) {
    return (
      <Box
        sx={{ width: '100%', boxShadow: 1, borderRadius: 1, p: 4 }}
        display="flex"
        justifyContent="center"
        flexDirection="column"
        alignItems="center"
      >
        <Typography variant="h6" gutterBottom component="div">
          Banner not available
        </Typography>
        <Button variant="contained" onClick={addNewBanner}>
          Add New Banner
        </Button>
      </Box>
    );
  }

  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} stickyHeader>
        <TableHead>
          <TableRow>
            <TableCell>Title</TableCell>
            <TableCell align="right">Description</TableCell>
            <TableCell align="right">Link</TableCell>
            <TableCell align="right">Status</TableCell>
            <TableCell align="right">Action</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow
              key={row.id}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell component="th" scope="row">
                {row.title}
              </TableCell>
              <TableCell align="right">{row.description}</TableCell>
              <TableCell align="right">{row.link}</TableCell>
              <TableCell align="right">
                <Switch
                  color="success"
                  checked={row.status ? true : false}
                  onChange={() => handleStatusToggle(row)}
                />
              </TableCell>
              <TableCell align="right">
                <Stack direction="row" spacing={1} justifyContent={'flex-end'}>
                  <Tooltip title={`Edit ${row.title}`}>
                    <IconButton
                      aria-label="edit"
                      onClick={() => handleEditBanner(row)}
                    >
                      <EditIcon />
                    </IconButton>
                  </Tooltip>
                  <Tooltip title={`Delete ${row.title}`}>
                    <IconButton
                      aria-label="delete"
                      color="error"
                      onClick={() => handleDelete(row)}
                    >
                      <DeleteIcon />
                    </IconButton>
                  </Tooltip>
                </Stack>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      <PaginationBox paginationData={paginationData} />
    </TableContainer>
  );
};

export default BannerList;
