import React, { useEffect, useState } from 'react';

import * as yup from 'yup';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Divider from '@mui/material/Divider';
import TextField from '@mui/material/TextField';
import LoadingButton from '@mui/lab/LoadingButton';
import FormControl from '@mui/material/FormControl';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import DesktopDatePicker from '@mui/lab/DesktopDatePicker';
import LocalizationProvider from '@mui/lab/LocalizationProvider';

import { useFormik } from 'formik';
import Modal from '../../../common/Modal';
import { reportList } from '../reportList';
import TextInfo from '../../../common/TextInfo';
import useAxios from '../../../../hooks/useAxios';
import useAlert from '../../../../hooks/useAlert';
import { __updateTenant } from '../../../../api/tenant';
import InputFieldBox from '../../../common/InputFieldBox';
import { getFormatDate } from '../../../../utils/date';

const EditTenant = ({ data, isOpen, onModalClose }) => {
  const reportPermissionList = {};

  reportList.forEach((feature) => {
    reportPermissionList[feature.id] = false;
  });

  const { setAlertMessage } = useAlert();
  const {
    url,
    name,
    email,
    reg_no,
    address,
    org_code,
    features,
    phone_nos,
    started_at,
    valid_until,
    id: tenant_id,
  } = data;
  let form = {
    url: url || '',
    name: name || '',
    email: email || '',
    reg_no: reg_no || '',
    address: address || '',
    org_code: org_code || '',
    features: features || '',
    phone_nos: phone_nos || '',
    started_at: started_at || null,
    valid_until: valid_until || null,
  };

  if (features && features.reports && features.reports.length) {
    features.reports.forEach((report) => {
      reportPermissionList[report] = true;
    });
  }
  const [reportPermission, setReportPermission] =
    useState(reportPermissionList);

  const validationSchema = yup.object({
    name: yup.string('Enter name').required('The name field is required.'),
    org_code: yup
      .string('Enter organization code')
      .required('The organization code is required.'),
    valid_until: yup
      .string('Choose valid until date')
      .nullable()
      .required('The valid until is required.'),
    email: yup.string('Enter email').email('Enter valid email'),
    link: yup
      .string()
      .matches(
        /((https?):\/\/)?(www.)?[a-z0-9]+(\.[a-z]{2,}){1,3}(#?\/?[a-zA-Z0-9#]+)*\/?(\?[a-zA-Z0-9-_]+=[a-zA-Z0-9-%]+&?)?$/,
        'Enter valid link'
      ),
  });

  const formik = useFormik({
    initialValues: {
      ...form,
    },
    validationSchema: validationSchema,
    onSubmit: () => {
      call();
    },
  });

  const [response, call, errors, loading] = useAxios({
    ...__updateTenant(tenant_id),
    data: formik.values,
  });

  useEffect(() => {
    if (!response) return;
    if (response.status) {
      onModalClose({ success: true });
      setAlertMessage({ type: 'success', message: response.message });
    } else if (response.errors && Object.keys(response.errors).length) {
      Object.keys(response.errors).forEach((error) => {
        formik.setFieldError(error, response.errors[error]);
      });
    }
  }, [response]);

  const handleFeatureChange = (id) => {
    const foundIndex = formik.values.features.reports.findIndex(
      (each) => each === id
    );
    if (foundIndex !== -1) {
      setReportPermission({ ...reportPermission, [id]: false });
      formik.values.features.reports.splice(foundIndex, 1);
    } else if (foundIndex === -1) {
      setReportPermission({ ...reportPermission, [id]: true });
      formik.values.features.reports.push(id);
    }
  };

  return (
    <Modal
      isOpen={isOpen}
      onModalClose={() => {
        formik.resetForm();
        onModalClose();
      }}
      title="Update Tenant"
    >
      <Box
        component="form"
        noValidate
        autoComplete="off"
        onSubmit={formik.handleSubmit}
      >
        <Grid container rowSpacing={2} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
          <Grid item xs={12}>
            <InputFieldBox
              id="name"
              name="name"
              label="Name"
              error={formik.errors.name}
              value={formik.values.name}
              onChange={formik.handleChange}
              isTouched={formik.touched.name}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              label="Code"
              id="org_code"
              name="org_code"
              error={formik.errors.org_code}
              value={formik.values.org_code}
              onChange={formik.handleChange}
              isTouched={formik.touched.org_code}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              id="email"
              name="email"
              label="Email"
              error={formik.errors.email}
              value={formik.values.email}
              onChange={formik.handleChange}
              isTouched={formik.touched.email}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              id="address"
              name="address"
              label="Address"
              error={formik.errors.address}
              value={formik.values.address}
              onChange={formik.handleChange}
              isTouched={formik.touched.address}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              id="reg_no"
              name="reg_no"
              label="Registration No."
              error={formik.errors.reg_no}
              value={formik.values.reg_no}
              onChange={formik.handleChange}
              isTouched={formik.touched.reg_no}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              id="url"
              name="url"
              label="Url"
              error={formik.errors.url}
              value={formik.values.url}
              onChange={formik.handleChange}
              isTouched={formik.touched.url}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              id="phone_nos"
              name="phone_nos"
              label="Phone No."
              onChange={formik.handleChange}
              error={formik.errors.phone_nos}
              value={formik.values.phone_nos}
              isTouched={formik.touched.phone_nos}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <FormControl sx={{ width: '100%' }}>
              <LocalizationProvider dateAdapter={AdapterDateFns}>
                <DesktopDatePicker
                  views={['day']}
                  name="started_at"
                  label="Start Date"
                  value={formik.values.started_at}
                  renderInput={(params) => <TextField {...params} />}
                  onChange={(date) => {
                    formik.setFieldValue('started_at', getFormatDate(date));
                  }}
                />
                {formik.touched.started_at &&
                Boolean(formik.errors.started_at) ? (
                  <TextInfo text={formik.errors.started_at} />
                ) : (
                  ''
                )}
              </LocalizationProvider>
            </FormControl>
          </Grid>
          <Grid item xs={12} md={6}>
            <FormControl sx={{ width: '100%' }}>
              <LocalizationProvider dateAdapter={AdapterDateFns}>
                <DesktopDatePicker
                  name="valid_until"
                  label="Valid Until"
                  value={formik.values.valid_until}
                  renderInput={(params) => <TextField {...params} />}
                  onChange={(date) =>
                    formik.setFieldValue('valid_until', getFormatDate(date))
                  }
                />
                {formik.touched.valid_until &&
                Boolean(formik.errors.valid_until) ? (
                  <TextInfo error text={formik.errors.valid_until} />
                ) : (
                  ''
                )}
              </LocalizationProvider>
            </FormControl>
          </Grid>

          <Grid item xs={12}>
            <Divider textAlign="left">Report Permission</Divider>
          </Grid>
          <Grid item container rowSpacing={1} columnSpacing={{ xs: 1 }}>
            {reportList.map((report) => (
              <Grid item xs={12} md={6} key={report.id}>
                <InputFieldBox
                  key={formik.values.features.reports.length}
                  isCheckbox
                  id={report.id}
                  name={report.id}
                  label={report.name}
                  value={reportPermission[report.id]}
                  onChange={() => {
                    handleFeatureChange(report.id);
                  }}
                />
              </Grid>
            ))}
          </Grid>
          <Grid item xs={12}>
            <LoadingButton
              type="submit"
              size="large"
              loading={loading}
              variant="contained"
              fullWidth
            >
              Update Tenant
            </LoadingButton>
          </Grid>
        </Grid>
      </Box>
    </Modal>
  );
};

export default EditTenant;
