import React, { useEffect } from 'react';

import * as yup from 'yup';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import LoadingButton from '@mui/lab/LoadingButton';

import { useFormik } from 'formik';
import Modal from '../../../common/Modal';
import useAlert from '../../../../hooks/useAlert';
import useAxios from '../../../../hooks/useAxios';
import { __updateMessages } from '../../../../api/message';
import InputFieldBox from '../../../common/InputFieldBox';

const EditMessage = ({ data, isOpen, onModalClose }) => {
  const { setAlertMessage } = useAlert();
  const { id: message_id, title, description, link, featured, status } = data;
  let form = {
    title,
    status,
    featured,
    link: link || '',
    description: description || '',
  };

  const validationSchema = yup.object({
    title: yup.string('Enter title').required('Title is required'),
    link: yup
      .string()
      .matches(
        /((https?):\/\/)?(www.)?[a-z0-9]+(\.[a-z]{2,}){1,3}(#?\/?[a-zA-Z0-9#]+)*\/?(\?[a-zA-Z0-9-_]+=[a-zA-Z0-9-%]+&?)?$/,
        'Enter valid link'
      ),
  });

  const formik = useFormik({
    initialValues: {
      ...form,
    },
    validationSchema: validationSchema,
    onSubmit: () => {
      call();
    },
  });

  const [response, call, errors, loading] = useAxios({
    ...__updateMessages(message_id),
    data: formik.values,
  });

  useEffect(() => {
    if (!response) return;
    if (response.status) {
      setAlertMessage({ type: 'success', message: response.message });
      onModalClose({ success: true });
    } else if (response.errors && Object.keys(response.errors).length) {
      Object.keys(response.errors).forEach((error) => {
        formik.setFieldError(error, response.errors[error]);
      });
    }
  }, [response]);

  return (
    <Modal
      isOpen={isOpen}
      onModalClose={() => {
        formik.resetForm();
        onModalClose();
      }}
      title="Update Message"
    >
      <Box
        component="form"
        noValidate
        autoComplete="off"
        onSubmit={formik.handleSubmit}
      >
        <Grid container rowSpacing={2} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
          <Grid item xs={12}>
            <InputFieldBox
              id="title"
              name="title"
              label="Title"
              error={formik.errors.title}
              value={formik.values.title}
              onChange={formik.handleChange}
              isTouched={formik.touched.title}
            />
          </Grid>
          <Grid item xs={12}>
            <InputFieldBox
              id="description"
              name="description"
              label="Description"
              error={formik.errors.description}
              value={formik.values.description}
              onChange={formik.handleChange}
              isTouched={formik.touched.description}
            />
          </Grid>
          <Grid item xs={12}>
            <InputFieldBox
              id="link"
              name="link"
              label="Link"
              error={formik.errors.link}
              value={formik.values.link}
              onChange={formik.handleChange}
              isTouched={formik.touched.link}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              isCheckbox
              id="status"
              name="status"
              label="Status"
              error={formik.errors.status}
              value={formik.values.status}
              onChange={(data) => {
                formik.setFieldValue('status', +data.target.checked);
              }}
              isTouched={formik.touched.status}
            />
          </Grid>
          <Grid item xs={12}>
            <LoadingButton
              type="submit"
              size="large"
              loading={loading}
              variant="contained"
              fullWidth
            >
              Update Message
            </LoadingButton>
          </Grid>
        </Grid>
      </Box>
    </Modal>
  );
};

export default EditMessage;
