import React, { useEffect } from 'react';
import { Link } from 'react-router-dom';

import { __logout } from '../../api/auth';
import useAxios from '../../hooks/useAxios';

import List from '@mui/material/List';
import Divider from '@mui/material/Divider';
import Toolbar from '@mui/material/Toolbar';
import ListItem from '@mui/material/ListItem';
import ImageIcon from '@mui/icons-material/Image';
import PersonIcon from '@mui/icons-material/Person';
import LogoutIcon from '@mui/icons-material/Logout';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import MessageIcon from '@mui/icons-material/Message';
import SettingsIcon from '@mui/icons-material/Settings';
import ManageAccountsIcon from '@mui/icons-material/ManageAccounts';

const SideNav = () => {
  const [response, call] = useAxios(__logout);
  const listItemTop = [
    { text: 'Tenant', to: '/tenant', icon: <ManageAccountsIcon /> },
    { text: 'Banners', to: '/banner', icon: <ImageIcon /> },
    { text: 'Messages', to: '/message', icon: <MessageIcon /> },
    { text: 'Users', to: '/user', icon: <PersonIcon /> },
  ];
  const listItemBottom = [
    { text: 'Setting', to: '/setting', icon: <SettingsIcon /> },
    {
      text: 'Logout',
      icon: <LogoutIcon />,
      hasNoLink: true,
      onclick: () => {
        localStorage.clear();
        call();
      },
    },
  ];

  useEffect(() => {
    if (response) {
      window.location.reload();
    }
  }, [response]);
  return (
    <div>
      <Toolbar />
      <Divider />
      <List>
        {listItemTop.map((list) => (
          <ListItem button key={list.text} component={Link} to={list.to}>
            <ListItemIcon>{list.icon}</ListItemIcon>
            <ListItemText primary={list.text} />
          </ListItem>
        ))}
      </List>
      <Divider />
      <List>
        {listItemBottom.map((list) =>
          !list.hasNoLink ? (
            <ListItem button key={list.text} component={Link} to={list.to}>
              <ListItemIcon>{list.icon}</ListItemIcon>
              <ListItemText primary={list.text} />
            </ListItem>
          ) : (
            <ListItem button key={list.text} onClick={list.onclick}>
              <ListItemIcon>{list.icon}</ListItemIcon>
              <ListItemText primary={list.text} />
            </ListItem>
          )
        )}
      </List>
    </div>
  );
};

export default SideNav;
