import React from 'react';

import Grid from '@mui/material/Grid';
import Chip from '@mui/material/Chip';
import Divider from '@mui/material/Divider';
import { reportList } from '../reportList';

const FeatureBox = ({ data }) => {
  const getReportPermissionName = (id) => {
    const foundReport = reportList.find((each) => each.id === id);
    if (foundReport) {
      return foundReport.name;
    }
    return id;
  };
  return (
    <>
      <Grid item xs={12}>
        <Divider textAlign="left">Features</Divider>
      </Grid>
      <Grid item xs={12}>
        {data.features.reports.map((report) => (
          <Chip
            label={getReportPermissionName(report)}
            color="success"
            key={report}
            sx={{ mr: 2, mb: 2 }}
          />
        ))}
      </Grid>
    </>
  );
};

export default FeatureBox;
