import React, { useEffect } from 'react';

import * as yup from 'yup';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import LoadingButton from '@mui/lab/LoadingButton';

import { useFormik } from 'formik';
import Modal from '../../../common/Modal';
import useAlert from '../../../../hooks/useAlert';
import useAxios from '../../../../hooks/useAxios';
import InputFieldBox from '../../../common/InputFieldBox';
import { __userPasswordUpdate } from '../../../../api/user';

const ResetPassword = ({ data, isOpen, onModalClose }) => {
  const { id: user_id, username } = data;
  const { setAlertMessage } = useAlert();
  let form = {
    new_password: '',
    new_password_confirmation: '',
  };

  const validationSchema = yup.object({
    new_password: yup
      .string('Enter new password')
      .min(8, 'Must contain 8 characters')
      .required('Enter new password'),
    new_password_confirmation: yup
      .string('Enter confirm password')
      .required('Enter confirm password')
      .oneOf([yup.ref('new_password')], 'Passwords does not match'),
  });

  const formik = useFormik({
    initialValues: {
      ...form,
    },
    validationSchema: validationSchema,
    onSubmit: () => {
      call();
    },
  });

  const [response, call, errors, loading] = useAxios({
    ...__userPasswordUpdate(user_id),
    data: formik.values,
  });

  useEffect(() => {
    if (!response) return;
    if (response.status) {
      formik.resetForm();
      setAlertMessage({ type: 'success', message: response.message });
      onModalClose({ success: true });
    } else if (response.errors && Object.keys(response.errors).length) {
      Object.keys(response.errors).forEach((error) => {
        formik.setFieldError(error, response.errors[error]);
      });
    }
  }, [response]);

  return (
    <Modal
      isOpen={isOpen}
      onModalClose={onModalClose}
      title={`Reset Password of ${username}`}
    >
      <Box
        component="form"
        noValidate
        autoComplete="off"
        onSubmit={formik.handleSubmit}
      >
        <Grid container rowSpacing={2} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
          <Grid item xs={12}>
            <InputFieldBox
              type="password"
              id="new_password"
              name="new_password"
              label="New Password"
              error={formik.errors.new_password}
              value={formik.values.new_password}
              onChange={formik.handleChange}
              isTouched={formik.touched.new_password}
            />
          </Grid>
          <Grid item xs={12}>
            <InputFieldBox
              type="password"
              label="Confirm Password"
              id="new_password_confirmation"
              name="new_password_confirmation"
              onChange={formik.handleChange}
              error={formik.errors.new_password_confirmation}
              value={formik.values.new_password_confirmation}
              isTouched={formik.touched.new_password_confirmation}
            />
          </Grid>
          <Grid item xs={12}>
            <LoadingButton
              type="submit"
              size="large"
              loading={loading}
              variant="contained"
              fullWidth
            >
              Reset Password
            </LoadingButton>
          </Grid>
        </Grid>
      </Box>
    </Modal>
  );
};

export default ResetPassword;
