import React, { useEffect } from 'react';

import * as yup from 'yup';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import LoadingButton from '@mui/lab/LoadingButton';

import { useFormik } from 'formik';
import Modal from '../../../common/Modal';
import { __addUser } from '../../../../api/user';
import useAlert from '../../../../hooks/useAlert';
import useAxios from '../../../../hooks/useAxios';
import InputFieldBox from '../../../common/InputFieldBox';

const AddNewUser = ({ title, isOpen, onModalClose }) => {
  const { setAlertMessage } = useAlert();
  const roles = [{ value: 2, label: 'Business Owner' }];
  let form = {
    role: 2,
    name: '',
    email: '',
    address: '',
    password: '',
    username: '',
    phone_nos: '',
    tenant_id: '',
    designation: '',
    profile_pic: '',
  };

  const validationSchema = yup.object({
    name: yup.string().required('Enter name'),
    role: yup.string().required('Choose role'),
    username: yup.string().required('Enter username'),
    email: yup.string().email('Enter valid email'),
    password: yup
      .string()
      .min(8, 'Must contain 8 characters')
      .required('Enter password'),
  });

  const formik = useFormik({
    initialValues: {
      ...form,
    },
    validationSchema: validationSchema,
    onSubmit: () => {
      call();
    },
  });

  const [response, call, errors, loading] = useAxios({
    ...__addUser,
    data: formik.values,
  });

  useEffect(() => {
    if (!response) return;
    if (response.status) {
      setAlertMessage({ type: 'success', message: response.message });
      formik.resetForm();
      onModalClose({ success: true });
    } else if (response.errors && Object.keys(response.errors).length) {
      Object.keys(response.errors).forEach((error) => {
        formik.setFieldError(error, response.errors[error]);
      });
    }
  }, [response]);

  return (
    <Modal
      isOpen={isOpen}
      onModalClose={() => {
        formik.resetForm();
        onModalClose();
      }}
      title={title}
    >
      <Box
        component="form"
        noValidate
        autoComplete="off"
        onSubmit={formik.handleSubmit}
      >
        <Grid container rowSpacing={2} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
          <Grid item xs={12}>
            <InputFieldBox
              id="name"
              name="name"
              label="Name"
              error={formik.errors.name}
              value={formik.values.name}
              onChange={formik.handleChange}
              isTouched={formik.touched.name}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              id="username"
              name="username"
              label="Username"
              error={formik.errors.username}
              value={formik.values.username}
              onChange={formik.handleChange}
              isTouched={formik.touched.username}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              type="password"
              id="password"
              name="password"
              label="Password"
              error={formik.errors.password}
              value={formik.values.password}
              onChange={formik.handleChange}
              isTouched={formik.touched.password}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              isSelect
              id="role"
              name="role"
              label="Role"
              options={roles}
              error={formik.errors.role}
              value={formik.values.role}
              onChange={formik.handleChange}
              isTouched={formik.touched.role}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              id="phone_nos"
              name="phone_nos"
              label="Phone No."
              error={formik.errors.phone_nos}
              value={formik.values.phone_nos}
              onChange={formik.handleChange}
              isTouched={formik.touched.phone_nos}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              id="designation"
              name="designation"
              label="Designation"
              error={formik.errors.designation}
              value={formik.values.designation}
              onChange={formik.handleChange}
              isTouched={formik.touched.designation}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              type="email"
              id="email"
              name="email"
              label="Email"
              error={formik.errors.email}
              value={formik.values.email}
              onChange={formik.handleChange}
              isTouched={formik.touched.email}
            />
          </Grid>
          <Grid item xs={12}>
            <InputFieldBox
              id="address"
              name="address"
              label="Address"
              error={formik.errors.address}
              value={formik.values.address}
              onChange={formik.handleChange}
              isTouched={formik.touched.address}
            />
          </Grid>
          <Grid item xs={12}>
            <LoadingButton
              type="submit"
              size="large"
              loading={loading}
              variant="contained"
              fullWidth
            >
              {title}
            </LoadingButton>
          </Grid>
        </Grid>
      </Box>
    </Modal>
  );
};

export default AddNewUser;
