import React, { useEffect, useState } from 'react';

import * as yup from 'yup';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import LoadingButton from '@mui/lab/LoadingButton';

import { useFormik } from 'formik';
import Modal from '../../../common/Modal';
import useAlert from '../../../../hooks/useAlert';
import useAxios from '../../../../hooks/useAxios';
import { __addNewBanner } from '../../../../api/banner';

import InputFieldBox from '../../../common/InputFieldBox';
import FileUploadInput from '../../../common/FileUploadInput';

const AddNewBanner = ({ isOpen, onModalClose }) => {
  const types = [
    { value: 1, label: 'Header Banner' },
    { value: 2, label: 'Body Banner' },
  ];
  const { setAlertMessage } = useAlert();
  const [formData, setFormData] = useState(null);
  let form = {
    type: 1,
    title: '',
    status: 1,
    banner_image: null,
    link: '',
    description: '',
  };
  const FILE_SIZE = 8000 * 8000;
  const SUPPORTED_FORMATS = [
    'image/jpg',
    'image/jpeg',
    'image/gif',
    'image/png',
  ];

  const validationSchema = yup.object({
    title: yup.string('Enter title').required('Title is required'),
    link: yup
      .string()
      .matches(
        /((https?):\/\/)?(www.)?[a-z0-9]+(\.[a-z]{2,}){1,3}(#?\/?[a-zA-Z0-9#]+)*\/?(\?[a-zA-Z0-9-_]+=[a-zA-Z0-9-%]+&?)?$/,
        'Enter valid link'
      ),
    banner_image: yup
      .mixed()
      .required('Banner image is required')
      .test(
        'fileSize',
        'File too large',
        (value) => value && value.size <= FILE_SIZE
      )
      .test(
        'fileFormat',
        'Unsupported Format',
        (value) => value && SUPPORTED_FORMATS.includes(value.type)
      ),
  });

  const formik = useFormik({
    initialValues: {
      ...form,
    },
    validationSchema: validationSchema,
    onSubmit: (values) => {
      const data = new FormData();
      Object.keys(values).forEach((key) => data.append(key, values[key]));
      setFormData(data);
    },
  });

  useEffect(() => {
    if (formData) {
      call();
    }
  }, [formData]);

  const [response, call, , loading] = useAxios({
    ...__addNewBanner,
    data: formData,
  });

  useEffect(() => {
    if (!response) return;
    if (response.status) {
      setAlertMessage({ type: 'success', message: response.message });
      formik.resetForm();
      onModalClose({ success: true });
    } else if (response.errors && Object.keys(response.errors).length) {
      Object.keys(response.errors).forEach((error) => {
        formik.setFieldError(error, response.errors[error]);
      });
    }
  }, [response]);

  return (
    <Modal
      isOpen={isOpen}
      onModalClose={() => {
        formik.resetForm();
        onModalClose();
      }}
      title="Add New Banner"
    >
      <Box
        component="form"
        noValidate
        autoComplete="off"
        onSubmit={formik.handleSubmit}
        ContentType="multipart/form-data"
      >
        <Grid container rowSpacing={2} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
          <Grid item xs={12}>
            <InputFieldBox
              id="title"
              name="title"
              label="Title"
              error={formik.errors.title}
              value={formik.values.title}
              onChange={formik.handleChange}
              isTouched={formik.touched.title}
            />
          </Grid>
          <Grid item xs={12}>
            <InputFieldBox
              id="description"
              name="description"
              label="Description"
              error={formik.errors.description}
              value={formik.values.description}
              onChange={formik.handleChange}
              isTouched={formik.touched.description}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              isSelect
              id="type"
              name="type"
              label="Type"
              options={types}
              error={formik.errors.type}
              value={formik.values.type}
              onChange={formik.handleChange}
              isTouched={formik.touched.type}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              id="link"
              name="link"
              label="Link"
              error={formik.errors.link}
              value={formik.values.link}
              onChange={formik.handleChange}
              isTouched={formik.touched.link}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <FileUploadInput
              name="banner_image"
              id="banner_image"
              label="Upload Banner Image"
              error={formik.errors.banner_image}
              isTouched={formik.touched.banner_image}
              onChange={(e) => {
                formik.setFieldValue('banner_image', e.currentTarget.files[0]);
              }}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              isCheckbox
              id="status"
              name="status"
              label="Status"
              error={formik.errors.status}
              value={formik.values.status}
              onChange={(data) => {
                formik.setFieldValue('status', +data.target.checked);
              }}
              isTouched={formik.touched.status}
            />
          </Grid>
          <Grid item xs={12}>
            <LoadingButton
              type="submit"
              size="large"
              loading={loading}
              variant="contained"
              fullWidth
            >
              Add New Banner
            </LoadingButton>
          </Grid>
        </Grid>
      </Box>
    </Modal>
  );
};

export default AddNewBanner;
