const __getUsers = {
  method: 'GET',
  url: '/users',
};

const __updateUser = (user_id) => {
  return {
    method: 'PUT',
    url: `/users/${user_id}`,
  };
};

const __addUser = {
  method: 'POST',
  url: '/users',
};

const __userStatusToggle = (user_id) => {
  return {
    method: 'PATCH',
    url: `/users/${user_id}/toggleStatus`,
  };
};

const __userPasswordUpdate = (user_id) => {
  return {
    method: 'PATCH',
    url: `users/${user_id}/updatePassword`,
  };
};

const __selfPasswordUpdate = (user_id) => {
  return {
    method: 'PATCH',
    url: `users/${user_id}/updateSelfPassword`,
  };
};

export {
  __addUser,
  __getUsers,
  __updateUser,
  __userStatusToggle,
  __selfPasswordUpdate,
  __userPasswordUpdate,
};
