import { useNavigate } from 'react-router-dom';
import React, { useState, useEffect } from 'react';

import Title from '../../common/Title';
import useAxios from '../../../hooks/useAxios';
import {
  __getTenants,
  __tenantStatusToggle,
  __updateTenantApiKey,
} from '../../../api/tenant';

import Box from '@mui/material/Box';
import TenantList from './list/TenantList';
import EditTenant from './edit/EditTenant';
import AddNewTenant from './add/AddNewTenant';
import useAlert from '../../../hooks/useAlert';
import AddNewButton from '../../common/AddNewButton';
import AlertDialog from '../../common/AlertDialog';

const TenantView = () => {
  let navigate = useNavigate();
  const { setAlertMessage } = useAlert();
  const [editData, setEditData] = useState({});
  const [dialogData, setDialogData] = useState({});
  const [openDialog, setOpenDialog] = useState(false);
  const [apiDialogData, setApiDialogData] = useState({});
  const [openApiDialog, setOpenApiDialog] = useState(false);
  const [response, call] = useAxios({ ...__getTenants });
  const [openAddModal, setOpenAddModal] = useState(false);
  const [tenantStatusToggleId, setTenantStatusToggleId] = useState(null);
  const [tenantApiKeyUpdateId, setTenantApiKeyUpdateId] = useState(null);
  const [response2, call2] = useAxios({
    ...__tenantStatusToggle(tenantStatusToggleId),
  });
  const [response3, call3] = useAxios({
    ...__updateTenantApiKey(tenantApiKeyUpdateId),
  });

  useEffect(() => {
    call();
  }, []);

  useEffect(() => {
    if (response2 && response2.status) {
      setAlertMessage({ type: 'success', message: response.message });
      call();
    }
  }, [response2]);

  useEffect(() => {
    if (response3 && response3.status) {
      setAlertMessage({ type: 'success', message: response.message });
      handleTenantDetailsShow({ ...response3.data, afterUpdateApi: true });
    }
  }, [response3]);

  useEffect(() => {
    if (tenantStatusToggleId !== null) {
      call2();
      setTimeout(() => {
        setTenantStatusToggleId(null);
      }, 100);
    }
  }, [tenantStatusToggleId]);

  useEffect(() => {
    if (tenantApiKeyUpdateId !== null) {
      call3();
      setTimeout(() => {
        setTenantApiKeyUpdateId(null);
      }, 100);
    }
  }, [tenantApiKeyUpdateId]);

  const openEditTenantModal = (data) => {
    setEditData({ ...data });
  };

  const onEditTenantModalClose = (data) => {
    if (data && data.success) {
      call();
    }
    setEditData({});
  };

  const openAddNewTenant = () => {
    setOpenAddModal(true);
  };

  const OnAddNewTenantModalClose = (data) => {
    if (data && data.success) {
      call();
    }
    setOpenAddModal(false);
  };

  const handleStatusToggle = (data) => {
    setDialogData({ ...data });
    setOpenDialog(true);
  };

  const handleApiUpdateTrigger = (data) => {
    setApiDialogData({ ...data });
    setOpenApiDialog(true);
  };

  const onEnableDisableConfirm = (data) => {
    setTenantStatusToggleId(data.id);
  };

  const handleTenantApiKeyUpdate = (data) => {
    setTenantApiKeyUpdateId(data.id);
  };

  const handleTenantDetailsShow = (data) => {
    setTimeout(() => {
      navigate(`/tenant/${data.id}`);
    }, 100);
  };

  return (
    <>
      <Box sx={{ width: '100%' }}>
        <Title variant="h5" content="Tenant" gutterBottom />
        {response ? (
          <>
            <TenantList
              response={response}
              addNewTenant={openAddNewTenant}
              handleEditTenant={openEditTenantModal}
              handleStatusToggle={handleStatusToggle}
              handleApiUpdateTrigger={handleApiUpdateTrigger}
            />
            <AlertDialog
              dialogClose={(data) => {
                if (data && data.confirm) {
                  handleTenantApiKeyUpdate(data.item);
                }
                setOpenApiDialog(false);
                setTimeout(() => {
                  setApiDialogData({});
                }, 200);
              }}
              data={apiDialogData}
              isOpen={openApiDialog}
              cancelText="No, Cancel"
              confirmTextColor="success"
              confirmText="Yes"
              title={`Are you sure you want to update api key`}
              description={`Confirming this will disable current api key and generate new api key.`}
            />
            <AlertDialog
              dialogClose={(data) => {
                if (data && data.confirm) {
                  onEnableDisableConfirm(data.item);
                }
                setOpenDialog(false);
                setTimeout(() => {
                  setDialogData({});
                }, 200);
              }}
              data={dialogData}
              isOpen={openDialog}
              cancelText="No, Cancel"
              confirmTextColor={dialogData?.deleted_at ? 'success' : 'error'}
              confirmText={`Yes, ${
                dialogData?.deleted_at ? 'enable' : 'disable'
              }`}
              title={`Are you sure you want to ${
                dialogData?.deleted_at ? 'enable' : 'disable'
              }?`}
              description={`Confirming this will ${
                dialogData?.deleted_at ? 'enable' : 'disable'
              } ${dialogData?.name}.`}
            />
          </>
        ) : (
          <Title content="Please wait..." variant="subtitle2" gutterBottom />
        )}
      </Box>

      {Object.keys(editData).length ? (
        <EditTenant
          data={editData}
          onModalClose={onEditTenantModalClose}
          isOpen={!!Object.keys(editData).length}
        />
      ) : (
        ''
      )}

      <AddNewTenant
        title="Add New Tenant"
        isOpen={openAddModal}
        onModalClose={OnAddNewTenantModalClose}
      />

      <AddNewButton title="Add New Tenant" handleClick={openAddNewTenant} />
    </>
  );
};

export default TenantView;
