const __getMessages = {
  method: 'GET',
  url: '/messages',
};

const __addMessages = {
  method: 'POST',
  url: '/messages',
};

const __updateMessages = (message_id) => {
  return {
    method: 'PUT',
    url: `/messages/${message_id}`,
  };
};

const __toggleStatus = (message_id) => {
  return {
    method: 'PATCH',
    url: `/messages/${message_id}`,
  };
};

const __deleteMessage = (message_id) => {
  return {
    method: 'DELETE',
    url: `/messages/${message_id}`,
  };
};

export {
  __getMessages,
  __addMessages,
  __toggleStatus,
  __deleteMessage,
  __updateMessages,
};
