import React from 'react';

import Title from '../../../common/Title';

import Grid from '@mui/material/Grid';
import Stack from '@mui/material/Stack';
import Divider from '@mui/material/Divider';
import LocalPhoneIcon from '@mui/icons-material/LocalPhone';
import EmailOutlinedIcon from '@mui/icons-material/EmailOutlined';
import LanguageOutlinedIcon from '@mui/icons-material/LanguageOutlined';
import LocationOnOutlinedIcon from '@mui/icons-material/LocationOnOutlined';

const ContactBox = ({ data }) => {
  return (
    <>
      <Grid item xs={12}>
        <Divider textAlign="left">Contact</Divider>
      </Grid>
      <Grid item xs={12} md={6}>
        <Stack
          direction="column"
          justifyContent="center"
          alignItems="flex-start"
          spacing={1}
        >
          {data.address ? (
            <Stack
              direction="row"
              justifyContent="flex-start"
              alignItems="center"
              spacing={1}
            >
              <LocationOnOutlinedIcon />
              <Title variant="body2" content={data.address} />
            </Stack>
          ) : (
            ''
          )}
          {data.phone_nos ? (
            <Stack
              direction="row"
              justifyContent="flex-start"
              alignItems="center"
              spacing={1}
            >
              <LocalPhoneIcon />
              <a href={`tel:${data.phone_nos}`}>
                <Title variant="body2" content={data.phone_nos} />
              </a>
            </Stack>
          ) : (
            ''
          )}
        </Stack>
      </Grid>
      <Grid item xs={12} md={6}>
        <Stack
          direction="column"
          justifyContent="center"
          alignItems="flex-start"
          spacing={1}
        >
          {data.email ? (
            <Stack
              direction="row"
              justifyContent="flex-start"
              alignItems="center"
              spacing={1}
            >
              <EmailOutlinedIcon />
              <a href={`mailto:${data.email}`} target="_blank" rel="noreferrer">
                <Title variant="body2" content={data.email} />
              </a>
            </Stack>
          ) : (
            ''
          )}
          {data.url ? (
            <Stack
              direction="row"
              justifyContent="flex-start"
              alignItems="center"
              spacing={1}
            >
              <LanguageOutlinedIcon />
              <a href={data.url} target="_blank" rel="noreferrer">
                <Title variant="body2" content={data.url} />
              </a>
            </Stack>
          ) : (
            ''
          )}
        </Stack>
      </Grid>
    </>
  );
};

export default ContactBox;
