import React, { useEffect } from 'react';

import * as yup from 'yup';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import LoadingButton from '@mui/lab/LoadingButton';

import { useFormik } from 'formik';
import Modal from '../../../common/Modal';
import { __updateUser } from '../../../../api/user';
import useAlert from '../../../../hooks/useAlert';
import useAxios from '../../../../hooks/useAxios';
import InputFieldBox from '../../../common/InputFieldBox';

const EditUser = ({ data, isOpen, onModalClose }) => {
  const { setAlertMessage } = useAlert();
  const {
    name,
    email,
    address,
    username,
    phone_nos,
    designation,
    profile_pic,
    id: user_id,
  } = data;

  let form = {
    name: name || '',
    email: email || '',
    address: address || '',
    username: username || '',
    phone_nos: phone_nos || '',
    designation: designation || '',
    profile_pic: profile_pic || '',
  };

  const validationSchema = yup.object({
    name: yup.string().required('Enter name'),
    username: yup.string().required('Enter username'),
    email: yup.string().email('Enter valid email'),
  });

  const formik = useFormik({
    initialValues: {
      ...form,
    },
    validationSchema: validationSchema,
    onSubmit: () => {
      call();
    },
  });

  const [response, call, errors, loading] = useAxios({
    ...__updateUser(user_id),
    data: formik.values,
  });

  useEffect(() => {
    if (!response) return;
    if (response.status) {
      setAlertMessage({ type: 'success', message: response.message });
      formik.resetForm();
      onModalClose({ success: true });
    } else if (response.errors && Object.keys(response.errors).length) {
      Object.keys(response.errors).forEach((error) => {
        formik.setFieldError(error, response.errors[error]);
      });
    }
  }, [response]);

  return (
    <Modal
      isOpen={isOpen}
      onModalClose={() => {
        formik.resetForm();
        onModalClose();
      }}
      title="Update User"
    >
      <Box
        component="form"
        noValidate
        autoComplete="off"
        onSubmit={formik.handleSubmit}
      >
        <Grid container rowSpacing={2} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
          <Grid item xs={12}>
            <InputFieldBox
              id="name"
              name="name"
              label="Name"
              error={formik.errors.name}
              value={formik.values.name}
              onChange={formik.handleChange}
              isTouched={formik.touched.name}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              id="username"
              name="username"
              label="Username"
              error={formik.errors.username}
              value={formik.values.username}
              onChange={formik.handleChange}
              isTouched={formik.touched.username}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              id="phone_nos"
              name="phone_nos"
              label="Phone No."
              error={formik.errors.phone_nos}
              value={formik.values.phone_nos}
              onChange={formik.handleChange}
              isTouched={formik.touched.phone_nos}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              id="designation"
              name="designation"
              label="Designation"
              error={formik.errors.designation}
              value={formik.values.designation}
              onChange={formik.handleChange}
              isTouched={formik.touched.designation}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              type="email"
              id="email"
              name="email"
              label="Email"
              error={formik.errors.email}
              value={formik.values.email}
              onChange={formik.handleChange}
              isTouched={formik.touched.email}
            />
          </Grid>
          <Grid item xs={12}>
            <InputFieldBox
              id="address"
              name="address"
              label="Address"
              error={formik.errors.address}
              value={formik.values.address}
              onChange={formik.handleChange}
              isTouched={formik.touched.address}
            />
          </Grid>
          <Grid item xs={12}>
            <LoadingButton
              type="submit"
              size="large"
              loading={loading}
              variant="contained"
              fullWidth
            >
              Update User
            </LoadingButton>
          </Grid>
        </Grid>
      </Box>
    </Modal>
  );
};

export default EditUser;
