import Axios from 'axios';
import { useState } from 'react';

import useAlert from './useAlert';
import useAuth from './useAuth';
import axios from '../config/axios';
import useLoading from './useLoading';
import getDefaultMessage from '../config/default-messages';
import { getFromStorage } from '../utils/storage';

let CancelToken = Axios.CancelToken;
let cancel;

export default function useAxios(options) {
  const { setAlertMessage } = useAlert();
  const [errors, setErrors] = useState({});
  const { resetAuthData } = useAuth();
  const { setIsLoading } = useLoading();
  const [loading, setLoading] = useState(false);
  const [response, setResponse] = useState(null);
  const call = async () => {
    try {
      cancel && cancel();
      setErrors({});
      setIsLoading({ isLoading: true });
      setLoading(true);
      const res = await axios({
        ...options,
        headers: {
          Authorization: getFromStorage('ACCESS_TOKEN_DATA'),
        },
        cancelToken: new CancelToken((c) => {
          cancel = c;
        }),
      });
      const data = typeof res.data === 'object' ? res.data : {};

      data.message = data.message
        ? data.message
        : getDefaultMessage(res.status);

      setResponse({ ...data, status: true });
      setErrors(data.errors ? data.errors : {});

      if (data) {
        setLoading(false);
        setIsLoading({ isLoading: false });
      }
      return { ...data };
    } catch (error) {
      if (error.response) {
        if (error.response.status === 401) {
          resetAuthData();
          setTimeout(() => {
            window.location.reload();
          }, 10);
        }
        const { data } = error.response;

        data.message = data.message
          ? data.message
          : getDefaultMessage(error.response.status);
        setErrors(data.errors ? data.errors : {});
        setAlertMessage({ type: 'error', message: data.message });
        setResponse({ ...data, status: false });
        setLoading(false);
        setIsLoading({ isLoading: false });
      }
      return false;
    }
  };

  return [response, call, errors, loading];
}
