import React, { useEffect, useState } from 'react';

import useAxios from '../../../hooks/useAxios';
import { __getUsers } from '../../../api/user';

import Box from '@mui/material/Box';
import UserList from './list/UserList';
import Title from '../../common/Title';
import EditUser from './edit/EditUser';
import AddNewUser from './add/AddNewUser';
import AddNewButton from '../../common/AddNewButton';
import ResetPassword from './reset-password/ResetPassword';

const UserView = () => {
  const [editData, setEditData] = useState({});
  const [openAddModal, setOpenAddModal] = useState();
  const [passwordChangeUserData, setPasswordChangeUserData] = useState({});
  const [response, call] = useAxios({ ...__getUsers });
  useEffect(() => {
    call();
  }, []);

  const handlePasswordChange = (user) => {
    setPasswordChangeUserData({ ...user });
  };

  const onChangePasswordModalClose = () => {
    setPasswordChangeUserData({});
  };

  const openAddNewUser = () => {
    setOpenAddModal(true);
  };

  const OnAddNewUserModalClose = () => {
    setOpenAddModal(false);
  };

  const openEditUserModal = (data) => {
    setEditData({ ...data });
  };

  const onEditUserModalClose = (data) => {
    if (data && data.success) {
      call();
    }
    setEditData({});
  };

  return (
    <>
      <Box sx={{ width: '100%' }}>
        <Title content="Users" variant="h5" />
        {response ? (
          <UserList
            response={response}
            addNewUser={openAddNewUser}
            handleEditUser={openEditUserModal}
            handlePasswordChange={handlePasswordChange}
          />
        ) : (
          <Title content="Please wait..." variant="subtitle2" gutterBottom />
        )}
      </Box>
      <AddNewUser
        title="Add New User"
        isOpen={openAddModal}
        onModalClose={OnAddNewUserModalClose}
      />
      {Object.keys(passwordChangeUserData).length ? (
        <ResetPassword
          data={passwordChangeUserData}
          isOpen={!!Object.keys(passwordChangeUserData).length}
          onModalClose={onChangePasswordModalClose}
        />
      ) : (
        ''
      )}
      {Object.keys(editData).length ? (
        <EditUser
          data={editData}
          onModalClose={onEditUserModalClose}
          isOpen={!!Object.keys(editData).length}
        />
      ) : (
        ''
      )}
      <AddNewButton title="Add New User" handleClick={openAddNewUser} />
    </>
  );
};
export default UserView;
