import React, { useEffect, useState } from 'react';

import * as yup from 'yup';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Divider from '@mui/material/Divider';
import TextField from '@mui/material/TextField';
import LoadingButton from '@mui/lab/LoadingButton';
import FormControl from '@mui/material/FormControl';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import DesktopDatePicker from '@mui/lab/DesktopDatePicker';
import LocalizationProvider from '@mui/lab/LocalizationProvider';

import { useFormik } from 'formik';
import Modal from '../../../common/Modal';
import { reportList } from '../reportList';
import TextInfo from '../../../common/TextInfo';
import useAxios from '../../../../hooks/useAxios';
import useAlert from '../../../../hooks/useAlert';
import { getFormatDate } from '../../../../utils/date';
import { __addNewTenant } from '../../../../api/tenant';
import InputFieldBox from '../../../common/InputFieldBox';

const AddNewTenant = ({ title, isOpen, onModalClose }) => {
  const [reportPermission, setReportPermission] = useState({
    sales_summary_report: true,
    top_sales_by_quantity_report: true,
    top_sales_by_amount_report: true,
    profit_by_product_report: true,
    top_loyalty_member_report: true,
    top_discount_member_report: true,
    store_wise_sales_summary_report: true,
  });
  const { setAlertMessage } = useAlert();

  let form = {
    tenant: {
      url: '',
      name: '',
      email: '',
      reg_no: '',
      address: '',
      org_code: '',
      phone_nos: '',
      started_at: null,
      valid_until: null,
      features: { reports: [...Object.keys(reportPermission)] },
    },
    user: {
      username: '',
      password: '',
      password_confirmation: '',
    },
  };

  const validationSchema = yup.object({
    tenant: yup.object({
      name: yup.string('Enter name').required('The name field is required.'),
      org_code: yup
        .string('Enter organization code')
        .required('The organization code is required.'),
      valid_until: yup
        .string('Choose valid until date')
        .nullable()
        .required('The valid until is required.'),
      email: yup.string('Enter email').email('Enter valid email'),
      link: yup
        .string()
        .matches(
          /((https?):\/\/)?(www.)?[a-z0-9]+(\.[a-z]{2,}){1,3}(#?\/?[a-zA-Z0-9#]+)*\/?(\?[a-zA-Z0-9-_]+=[a-zA-Z0-9-%]+&?)?$/,
          'Enter valid link'
        ),
    }),
    user: yup.object({
      username: yup.string().required('Enter username'),
      password: yup
        .string()
        .min(8, 'Must contain 8 characters')
        .required('Enter password'),
      password_confirmation: yup
        .string('Enter confirm password')
        .required('Enter confirm password')
        .oneOf([yup.ref('password')], 'Passwords does not match'),
    }),
  });

  const formik = useFormik({
    initialValues: {
      ...form,
    },
    validationSchema: validationSchema,
    onSubmit: () => {
      call();
    },
  });

  const [response, call, errors, loading] = useAxios({
    ...__addNewTenant,
    data: formik.values,
  });

  useEffect(() => {
    if (!response) return;
    if (response.status) {
      formik.resetForm();
      onModalClose({ success: true });
      setAlertMessage({ type: 'success', message: response.message });
    } else if (response.errors && Object.keys(response.errors).length) {
      Object.keys(response.errors).forEach((error) => {
        formik.setFieldError(error, response.errors[error]);
      });
    }
  }, [response]);

  const handleFeatureChange = (id) => {
    const foundIndex = formik.values.tenant.features.reports.findIndex(
      (each) => each === id
    );
    if (foundIndex !== -1) {
      setReportPermission({ ...reportPermission, [id]: false });
      formik.values.tenant.features.reports.splice(foundIndex, 1);
    } else if (foundIndex === -1) {
      setReportPermission({ ...reportPermission, [id]: true });
      formik.values.tenant.features.reports.push(id);
    }
  };

  return (
    <Modal
      isOpen={isOpen}
      onModalClose={() => {
        formik.resetForm();
        onModalClose();
      }}
      title={title}
    >
      <Box
        component="form"
        noValidate
        autoComplete="off"
        onSubmit={formik.handleSubmit}
      >
        <Grid container rowSpacing={2} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
          <Grid item xs={12}>
            <InputFieldBox
              id="name"
              name="tenant.name"
              label="Name"
              error={formik.errors.tenant?.name}
              value={formik.values.tenant.name}
              onChange={formik.handleChange}
              isTouched={formik.touched.tenant?.name}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              label="Code"
              id="org_code"
              name="tenant.org_code"
              error={formik.errors.tenant?.org_code}
              value={formik.values.tenant.org_code}
              onChange={formik.handleChange}
              isTouched={formik.touched.tenant?.org_code}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              id="email"
              name="tenant.email"
              label="Email"
              error={formik.errors.tenant?.email}
              value={formik.values.tenant.email}
              onChange={formik.handleChange}
              isTouched={formik.touched.tenant?.email}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              id="address"
              name="tenant.address"
              label="Address"
              error={formik.errors.tenant?.address}
              value={formik.values.tenant.address}
              onChange={formik.handleChange}
              isTouched={formik.touched.tenant?.address}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              id="reg_no"
              name="tenant.reg_no"
              label="Registration No."
              error={formik.errors.tenant?.reg_no}
              value={formik.values.tenant.reg_no}
              onChange={formik.handleChange}
              isTouched={formik.touched.tenant?.reg_no}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              id="url"
              name="tenant.url"
              label="Url"
              error={formik.errors.tenant?.url}
              value={formik.values.tenant.url}
              onChange={formik.handleChange}
              isTouched={formik.touched.tenant?.url}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              id="phone_nos"
              name="tenant.phone_nos"
              label="Phone No."
              onChange={formik.handleChange}
              error={formik.errors.tenant?.phone_nos}
              value={formik.values.tenant.phone_nos}
              isTouched={formik.touched.tenant?.phone_nos}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <FormControl sx={{ width: '100%' }}>
              <LocalizationProvider dateAdapter={AdapterDateFns}>
                <DesktopDatePicker
                  name="tenant.started_at"
                  label="Start Date"
                  inputFormat="MM/dd/yyyy"
                  value={formik.values.tenant.started_at}
                  renderInput={(params) => <TextField {...params} />}
                  onChange={(date) =>
                    formik.setFieldValue(
                      'tenant.started_at',
                      getFormatDate(date)
                    )
                  }
                />
                {formik.touched.tenant?.started_at &&
                Boolean(formik.errors.tenant?.started_at) ? (
                  <TextInfo text={formik.errors.tenant?.started_at} />
                ) : (
                  ''
                )}
              </LocalizationProvider>
            </FormControl>
          </Grid>
          <Grid item xs={12} md={6}>
            <FormControl sx={{ width: '100%' }}>
              <LocalizationProvider dateAdapter={AdapterDateFns}>
                <DesktopDatePicker
                  name="tenant.valid_until"
                  label="Valid Until"
                  inputFormat="MM/dd/yyyy"
                  value={formik.values.tenant.valid_until}
                  renderInput={(params) => <TextField {...params} />}
                  onChange={(date) =>
                    formik.setFieldValue(
                      'tenant.valid_until',
                      getFormatDate(date)
                    )
                  }
                />
                {formik.touched.tenant?.valid_until &&
                Boolean(formik.errors.tenant?.valid_until) ? (
                  <TextInfo error text={formik.errors.tenant?.valid_until} />
                ) : (
                  ''
                )}
              </LocalizationProvider>
            </FormControl>
          </Grid>
          <Grid item xs={12}>
            <Divider textAlign="left">Assign Report Permission</Divider>
          </Grid>
          <Grid item container rowSpacing={1} columnSpacing={{ xs: 1 }}>
            {reportList.map((report) => (
              <Grid item xs={12} md={6} key={report.id}>
                <InputFieldBox
                  key={formik.values.tenant.features.reports.length}
                  isCheckbox
                  id={report.id}
                  name={report.id}
                  label={report.name}
                  value={reportPermission[report.id]}
                  onChange={() => {
                    handleFeatureChange(report.id);
                  }}
                />
              </Grid>
            ))}
          </Grid>
          <Grid item xs={12}>
            <Divider textAlign="left">Add Admin User</Divider>
          </Grid>
          <Grid item xs={12}>
            <InputFieldBox
              id="username"
              name="user.username"
              label="Username"
              error={formik.errors.user?.username}
              value={formik.values.user.username}
              onChange={formik.handleChange}
              isTouched={formik.touched.user?.username}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              type="password"
              id="password"
              name="user.password"
              label="Password"
              error={formik.errors.user?.password}
              value={formik.values.user.password}
              onChange={formik.handleChange}
              isTouched={formik.touched.user?.password}
            />
          </Grid>
          <Grid item xs={12} md={6}>
            <InputFieldBox
              type="password"
              label="Confirm Password"
              id="password_confirmation"
              name="user.password_confirmation"
              onChange={formik.handleChange}
              error={formik.errors.user?.password_confirmation}
              value={formik.values.user.password_confirmation}
              isTouched={formik.touched.user?.password_confirmation}
            />
          </Grid>
          <Grid item xs={12} mt={1}>
            <LoadingButton
              type="submit"
              size="large"
              loading={loading}
              variant="contained"
              fullWidth
            >
              {title}
            </LoadingButton>
          </Grid>
        </Grid>
      </Box>
    </Modal>
  );
};

export default AddNewTenant;
