import React, { useEffect, useState } from 'react';

import Title from '../../common/Title';
import BannerList from './list/BannerList';
import useAxios from '../../../hooks/useAxios';
import {
  __getBanners,
  __deleteBanner,
  __toggleStatus,
} from '../../../api/banner';

import Box from '@mui/material/Box';
import EditBanner from './edit/EditBanner';
import AddNewBanner from './add/AddNewBanner';
import useAlert from '../../../hooks/useAlert';
import AlertDialog from '../../common/AlertDialog';
import AddNewButton from '../../common/AddNewButton';

const BannerView = () => {
  const { setAlertMessage } = useAlert();
  const [editData, setEditData] = useState({});
  const [dialogData, setDialogData] = useState({});
  const [response, call] = useAxios({ ...__getBanners });
  const [openAddModal, setOpenAddModal] = useState(false);
  const [bannerDeleteId, setBannerDeleteId] = useState(null);
  const [toggleStatusId, setToggleStatusId] = useState(null);
  const [response1, call1] = useAxios({ ...__deleteBanner(bannerDeleteId) });
  const [response2, call2] = useAxios({ ...__toggleStatus(toggleStatusId) });

  useEffect(() => {
    call();
  }, []);

  useEffect(() => {
    if (response1 && response1.status) {
      setAlertMessage({ type: 'success', message: response.message });
      call();
    }
  }, [response1]);

  useEffect(() => {
    if (response2 && response2.status) {
      setAlertMessage({ type: 'success', message: response.message });
      call();
    }
  }, [response2]);

  useEffect(() => {
    if (bannerDeleteId !== null) {
      call1();
      setTimeout(() => {
        setBannerDeleteId(null);
      }, 100);
    }
  }, [bannerDeleteId]);

  useEffect(() => {
    if (toggleStatusId !== null) {
      call2();
      setTimeout(() => {
        setToggleStatusId(null);
      }, 100);
    }
  }, [toggleStatusId]);

  const openAddNewBanner = () => {
    setOpenAddModal(true);
  };

  const OnAddNewBannerModalClose = (data) => {
    if (data && data.success) {
      call();
    }
    setOpenAddModal(false);
  };

  const openEditBannerModal = (data) => {
    setEditData({ ...data });
  };

  const onEditBannerModalClose = (data) => {
    if (data && data.success) {
      call();
    }
    setEditData({});
  };

  const handleDelete = (data) => {
    setDialogData({ ...data });
  };

  const onDeleteConfirm = (data) => {
    setBannerDeleteId(data.id);
  };

  const handleStatusToggle = (message) => {
    setToggleStatusId(message.id);
  };

  return (
    <>
      <Box sx={{ width: '100%' }}>
        <Title variant="h5" content="Banners" gutterBottom />
        {response ? (
          <>
            <BannerList
              response={response}
              addNewBanner={openAddNewBanner}
              handleStatusToggle={handleStatusToggle}
              handleEditBanner={openEditBannerModal}
              handleDelete={(data) => handleDelete(data)}
            />
            <AlertDialog
              dialogClose={(data) => {
                if (data && data.confirm) {
                  onDeleteConfirm(data.item);
                }
                setDialogData({});
              }}
              data={dialogData}
              isOpen={!!Object.keys(dialogData).length}
              cancelText="No, Cancel"
              confirmTextColor="error"
              confirmText="Yes, Delete"
              title="Are you sure you want to delete?"
              description="This process cannot be undo, once you delete it."
            />
          </>
        ) : (
          <Title content="Please wait..." variant="subtitle2" gutterBottom />
        )}
      </Box>
      {Object.keys(editData).length ? (
        <EditBanner
          data={editData}
          isOpen={!!Object.keys(editData).length}
          onModalClose={onEditBannerModalClose}
        />
      ) : (
        ''
      )}
      <AddNewBanner
        isOpen={openAddModal}
        onModalClose={OnAddNewBannerModalClose}
      />

      <AddNewButton title="Add New Banner" handleClick={openAddNewBanner} />
    </>
  );
};

export default BannerView;
