import React, { useState } from 'react';

import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Stack from '@mui/material/Stack';
import Divider from '@mui/material/Divider';
import Tooltip from '@mui/material/Tooltip';
import KeyIcon from '@mui/icons-material/Key';
import IconButton from '@mui/material/IconButton';
import ContentCopyIcon from '@mui/icons-material/ContentCopy';

const ApiKeyBox = ({ data }) => {
  const [copyToolTip, setCopyToolTip] = useState('Copy');

  const copyToClipboard = () => {
    var copyText = document.getElementById('tenantApiKeyInput');

    copyText.select();
    copyText.setSelectionRange(0, 99999);

    navigator.clipboard.writeText(copyText.value);
    setCopyToolTip('Copied');
    setTimeout(() => {
      setCopyToolTip('Copy');
    }, 2000);
  };

  return (
    <>
      {!data.afterUpdateApi ? (
        <Grid item xs={12}>
          <Divider textAlign="left">Api Key</Divider>
        </Grid>
      ) : (
        ''
      )}
      <Grid item xs={12}>
        <Stack
          direction="row"
          justifyContent="flex-start"
          alignItems="center"
          spacing={1}
        >
          <KeyIcon />
          <Tooltip title="Api Key">
            <Box
              sx={{
                width: '100%',
                padding: 1,
                border: 2,
                borderRadius: 2,
                borderColor: 'primary.main',
                textOverflow: 'ellipsis',
                overflow: 'hidden',
                color: 'primary.main',
              }}
            >
              <input
                type="text"
                readOnly
                value={data.api_key}
                id="tenantApiKeyInput"
                style={{ display: 'none' }}
              />
              {data.api_key}
            </Box>
          </Tooltip>
          <Tooltip title={copyToolTip}>
            <IconButton aria-label="copy" onClick={copyToClipboard}>
              <ContentCopyIcon />
            </IconButton>
          </Tooltip>
        </Stack>
      </Grid>
      {data.afterUpdateApi ? (
        <Grid item xs={12}>
          <Divider />
        </Grid>
      ) : (
        ''
      )}
    </>
  );
};

export default ApiKeyBox;
