const __getBanners = {
  method: 'GET',
  url: '/banners',
};

const __addNewBanner = {
  method: 'POST',
  url: '/banners',
};

const __updateBanner = (banner_id) => {
  return {
    method: 'POST',
    url: `/banners/${banner_id}`,
  };
};

const __deleteBanner = (banner_id) => {
  return {
    method: 'DELETE',
    url: `/banners/${banner_id}`,
  };
};

const __toggleStatus = (banner_id) => {
  return {
    method: 'PATCH',
    url: `/banners/${banner_id}`,
  };
};

export {
  __getBanners,
  __updateBanner,
  __deleteBanner,
  __toggleStatus,
  __addNewBanner,
};
