import { Navigate } from 'react-router-dom';
import React, { useEffect, useCallback } from 'react';

import * as yup from 'yup';
import { useFormik } from 'formik';

import { __login } from '../../api/auth';
import useAuth from '../../hooks/useAuth';
import useAxios from '../../hooks/useAxios';

import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import InputLabel from '@mui/material/InputLabel';
import Typography from '@mui/material/Typography';
import LoadingButton from '@mui/lab/LoadingButton';
import FormControl from '@mui/material/FormControl';
import OutlinedInput from '@mui/material/OutlinedInput';
import FormHelperText from '@mui/material/FormHelperText';

const Login = () => {
  const { auth, setUserData, setAccessTokenData } = useAuth();
  let form = { username: '', password: '' };

  const validationSchema = yup.object({
    username: yup
      .string('Enter your password')
      .required('Username is required'),
    password: yup
      .string('Enter your password')
      .required('Password is required'),
  });

  const formik = useFormik({
    initialValues: {
      ...form,
    },
    validationSchema: validationSchema,
    onSubmit: () => {
      call();
    },
  });

  const [response, call, errors, loading] = useAxios({
    ...__login,
    data: formik.values,
  });

  const authentication = useCallback(() => {
    setAccessTokenData(response.data.access_token);
    setUserData(response.data.user);
  }, [response]);

  useEffect(() => {
    if (!response) return;
    if (response.status) {
      setTimeout(() => {
        authentication();
      }, 10);
    } else if (Object.keys(response.errors).length) {
      Object.keys(response.errors).forEach((error) => {
        formik.setFieldError(error, response.errors[error]);
      });
    }
  }, [response]);

  if (auth.token) {
    return <Navigate to="/" />;
  }

  return (
    <Grid sx={{ maxWidth: '460px', mx: 'auto' }}>
      <Grid
        container
        direction="column"
        justifyContent="center"
        alignItems="center"
        minHeight="100vh"
        padding={2}
      >
        <Box
          component="form"
          sx={{
            width: '100%',
            boxShadow: 3,
            borderRadius: 2,
            p: 4,
          }}
          noValidate
          autoComplete="off"
          onSubmit={formik.handleSubmit}
        >
          <Typography
            variant="h4"
            gutterBottom
            component="div"
            sx={{ textAlign: 'center' }}
          >
            LOGIN
          </Typography>
          <Grid>
            <Grid item xs={12} sx={{ mb: 3, mt: 2 }}>
              <FormControl
                error={
                  formik.touched.username && Boolean(formik.errors.username)
                    ? true
                    : false
                }
                sx={{ width: '100%' }}
              >
                <InputLabel htmlFor="username">Username</InputLabel>
                <OutlinedInput
                  name="username"
                  label="Username"
                  id="username"
                  value={formik.values.username}
                  onChange={formik.handleChange}
                />
                {formik.touched.username && Boolean(formik.errors.username) ? (
                  <FormHelperText id="username-error-text">
                    {formik.errors.username}
                  </FormHelperText>
                ) : (
                  ''
                )}
              </FormControl>
            </Grid>
            <Grid item xs={12} sx={{ mb: 3 }}>
              <FormControl
                error={
                  formik.touched.password && Boolean(formik.errors.password)
                    ? true
                    : false
                }
                sx={{ width: '100%' }}
              >
                <InputLabel htmlFor="password">Password</InputLabel>
                <OutlinedInput
                  type="password"
                  name="password"
                  label="Password"
                  id="password"
                  value={formik.values.password}
                  onChange={formik.handleChange}
                />
                {formik.touched.password && Boolean(formik.errors.password) ? (
                  <FormHelperText id="password-error-text">
                    {formik.errors.password}
                  </FormHelperText>
                ) : (
                  ''
                )}
              </FormControl>
            </Grid>
            <LoadingButton
              type="submit"
              size="large"
              loading={loading}
              variant="contained"
              fullWidth
            >
              Login
            </LoadingButton>
          </Grid>
        </Box>
      </Grid>
    </Grid>
  );
};

export default Login;
