import { useParams } from 'react-router-dom';
import React, { useEffect, useState } from 'react';

import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Chip from '@mui/material/Chip';
import Stack from '@mui/material/Stack';
import Divider from '@mui/material/Divider';
import Tooltip from '@mui/material/Tooltip';

import ApiKeyBox from './ApiKeyBox';
import ContactBox from './ContactBox';
import FeatureBox from './FeatureBox';
import Title from '../../../common/Title';
import useAxios from '../../../../hooks/useAxios';
import { __getTenantDetails } from '../../../../api/tenant';

const Show = () => {
  const { id: tenant_id } = useParams();
  const [response, call] = useAxios({ ...__getTenantDetails(tenant_id) });
  const [data, setData] = useState({});
  useEffect(() => {
    call();
  }, []);

  useEffect(() => {
    if (response && response.status) {
      setData(response.data);
    }
  }, [response]);

  const title = (
    <Box display="flex" alignItems="center" justifyContent="space-between">
      {data.name}{' '}
      {data.deleted_at ? (
        <Chip label="disabled" color="error" size="small" />
      ) : (
        ''
      )}
    </Box>
  );

  if (!Object.keys(data).length) {
    return <p>Please Wait...</p>;
  }

  return (
    <Box>
      <Grid container rowSpacing={2} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
        {data.afterUpdateApi ? <ApiKeyBox data={data} /> : ''}
        <Grid item xs={12} md={6}>
          <Stack
            direction="column"
            justifyContent="center"
            alignItems="flex-start"
            spacing={1}
          >
            <Title variant="h6" content={`Code: ${data.org_code}`} />
            {data.started_at ? (
              <Tooltip title={data.started_at}>
                <Box>
                  Start Date:&nbsp;
                  <Chip label={data.started_at} variant="outlined" />
                </Box>
              </Tooltip>
            ) : (
              ''
            )}
            <Tooltip title={data.valid_until}>
              <Box>
                Valid Until:&nbsp;
                <Chip
                  label={data.valid_until}
                  color="error"
                  variant="outlined"
                />
              </Box>
            </Tooltip>
          </Stack>
        </Grid>
        {!data.afterUpdateApi ? <ApiKeyBox data={data} /> : ''}
        <FeatureBox data={data} />
        <ContactBox data={data} />
      </Grid>
    </Box>
  );
};

export default Show;
