import React, { useEffect } from 'react';

import * as yup from 'yup';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import LoadingButton from '@mui/lab/LoadingButton';

import { useFormik } from 'formik';
import useAuth from '../../../../hooks/useAuth';
import useAlert from '../../../../hooks/useAlert';
import useAxios from '../../../../hooks/useAxios';
import InputFieldBox from '../../../common/InputFieldBox';
import { __selfPasswordUpdate } from '../../../../api/user';

const ChangePassword = () => {
  const { setAlertMessage } = useAlert();
  const { user } = useAuth();
  let form = {
    old_password: '',
    new_password: '',
    new_password_confirmation: '',
  };

  const validationSchema = yup.object({
    old_password: yup
      .string('Enter old password')
      .required('Enter old password'),
    new_password: yup
      .string('Enter new password')
      .min(8, 'Must contain 8 characters')
      .required('Enter new password'),
    new_password_confirmation: yup
      .string('Enter confirm password')
      .required('Enter confirm password')
      .oneOf([yup.ref('new_password')], 'Passwords does not match'),
  });

  const formik = useFormik({
    initialValues: {
      ...form,
    },
    validationSchema: validationSchema,
    onSubmit: () => {
      call();
    },
  });

  const [response, call, errors, loading] = useAxios({
    ...__selfPasswordUpdate(user.id),
    data: formik.values,
  });

  useEffect(() => {
    if (!response) return;
    if (response.status) {
      formik.resetForm();
      setAlertMessage({ type: 'success', message: response.message });
    } else if (response.errors && Object.keys(response.errors).length) {
      Object.keys(response.errors).forEach((error) => {
        formik.setFieldError(error, response.errors[error]);
      });
    }
  }, [response]);

  return (
    <Box
      component="form"
      noValidate
      autoComplete="off"
      onSubmit={formik.handleSubmit}
      sx={{ maxWidth: '500px' }}
    >
      <Grid container rowSpacing={2} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
        <Grid item xs={12}>
          <InputFieldBox
            type="password"
            id="old_password"
            name="old_password"
            label="Old Password"
            error={formik.errors.old_password}
            value={formik.values.old_password}
            onChange={formik.handleChange}
            isTouched={formik.touched.old_password}
          />
        </Grid>
        <Grid item xs={12}>
          <InputFieldBox
            type="password"
            id="new_password"
            name="new_password"
            label="New Password"
            error={formik.errors.new_password}
            value={formik.values.new_password}
            onChange={formik.handleChange}
            isTouched={formik.touched.new_password}
          />
        </Grid>
        <Grid item xs={12}>
          <InputFieldBox
            type="password"
            label="Confirm Password"
            id="new_password_confirmation"
            name="new_password_confirmation"
            onChange={formik.handleChange}
            error={formik.errors.new_password_confirmation}
            value={formik.values.new_password_confirmation}
            isTouched={formik.touched.new_password_confirmation}
          />
        </Grid>
        <Grid item xs={12}>
          <LoadingButton
            type="submit"
            size="large"
            loading={loading}
            variant="contained"
            fullWidth
          >
            Change Password
          </LoadingButton>
        </Grid>
      </Grid>
    </Box>
  );
};

export default ChangePassword;
