import React, { useState, useEffect } from 'react';

import Box from '@mui/material/Box';
import Table from '@mui/material/Table';
import Paper from '@mui/material/Paper';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import Tooltip from '@mui/material/Tooltip';
import TableRow from '@mui/material/TableRow';
import EditIcon from '@mui/icons-material/Edit';
import TableCell from '@mui/material/TableCell';
import TableBody from '@mui/material/TableBody';
import TableHead from '@mui/material/TableHead';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import LockResetIcon from '@mui/icons-material/LockReset';
import TableContainer from '@mui/material/TableContainer';
import PaginationBox from '../../../common/PaginationBox';

const UserList = ({
  response,
  addNewUser,
  handleEditUser,
  handlePasswordChange,
}) => {
  const [rows, setRows] = useState([]);
  const [paginationData, setPaginationData] = useState({});
  useEffect(() => {
    if (response && response.status) {
      setRows(
        response.data.data
          .filter((each) => each.role !== 1)
          .map((each) => {
            const { name, email, tenant_name, role_name, username } = each;
            const eachRow = {
              id: each.id,
              name,
              email,
              username,
              role_name,
              tenant_name,
            };
            return eachRow;
          })
      );
      const {
        current_page,
        first_page_url,
        from,
        last_page,
        last_page_url,
        next_page_url,
        per_page,
        prev_page_url,
        to,
        total,
      } = response.data;
      setPaginationData({
        current_page,
        first_page_url,
        from,
        last_page,
        last_page_url,
        next_page_url,
        per_page,
        prev_page_url,
        to,
        total,
      });
    }
  }, [response]);

  if (!rows.length) {
    return (
      <Box
        sx={{ width: '100%', boxShadow: 1, borderRadius: 1, p: 4 }}
        display="flex"
        justifyContent="center"
        flexDirection="column"
        alignItems="center"
      >
        <Typography variant="h6" gutterBottom component="div">
          User not available
        </Typography>
        <Button variant="contained" onClick={addNewUser}>
          Add New User
        </Button>
      </Box>
    );
  }
  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} stickyHeader>
        <TableHead>
          <TableRow>
            <TableCell>Name</TableCell>
            <TableCell align="right">Email</TableCell>
            <TableCell align="right">Username</TableCell>
            <TableCell align="right">Role</TableCell>
            <TableCell align="right">Tenant</TableCell>
            <TableCell align="right">Action</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow
              key={row.name}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell component="th" scope="row">
                {row.name}
              </TableCell>
              <TableCell align="right">{row.email}</TableCell>
              <TableCell align="right">{row.username}</TableCell>
              <TableCell align="right">{row.role_name}</TableCell>
              <TableCell align="right">{row.tenant_name}</TableCell>
              <TableCell align="right">
                <Stack direction="row" spacing={1} justifyContent={'flex-end'}>
                  <Tooltip title={`Edit ${row.username}`}>
                    <IconButton
                      aria-label="edit"
                      onClick={() => handleEditUser(row)}
                    >
                      <EditIcon />
                    </IconButton>
                  </Tooltip>
                  <Tooltip title={`Reset Password of ${row.username}`}>
                    <IconButton
                      aria-label="reset password"
                      onClick={() => handlePasswordChange(row)}
                    >
                      <LockResetIcon />
                    </IconButton>
                  </Tooltip>
                </Stack>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>

      <PaginationBox paginationData={paginationData} />
    </TableContainer>
  );
};

export default UserList;
