const __getTenants = {
  method: 'GET',
  url: '/tenants',
};

const __addNewTenant = {
  method: 'POST',
  url: '/tenants',
};

const __updateTenant = (tenant_id) => {
  return {
    method: 'PUT',
    url: `/tenants/${tenant_id}`,
  };
};

const __getTenantDetails = (tenant_id) => {
  return {
    method: 'GET',
    url: `/tenants/${tenant_id}/show`,
  };
};

const __updateTenantApiKey = (tenant_id) => {
  return {
    method: 'PATCH',
    url: `/tenants/${tenant_id}`,
  };
};

const __tenantStatusToggle = (tenant_id) => {
  return {
    method: 'PATCH',
    url: `/tenants/${tenant_id}/disable`,
  };
};

export {
  __getTenants,
  __addNewTenant,
  __updateTenant,
  __getTenantDetails,
  __tenantStatusToggle,
  __updateTenantApiKey,
};
