import React, { useEffect, useState } from 'react';

import Title from '../../common/Title';
import MessageList from './list/MessageList';
import useAxios from '../../../hooks/useAxios';
import AddNewButton from '../../common/AddNewButton';
import {
  __getMessages,
  __deleteMessage,
  __toggleStatus,
} from '../../../api/message';

import Box from '@mui/material/Box';
import EditMessage from './edit/EditMessage';
import useAlert from '../../../hooks/useAlert';
import AddNewMessage from './add/AddNewMessage';
import AlertDialog from '../../common/AlertDialog';

const MessageView = () => {
  const { setAlertMessage } = useAlert();
  const [editData, setEditData] = useState({});
  const [dialogData, setDialogData] = useState({});
  const [openAddModal, setOpenAddModal] = useState(false);
  const [response, call] = useAxios({ ...__getMessages });
  const [toggleStatusId, setToggleStatusId] = useState(null);
  const [messageDeleteId, setMessageDeleteId] = useState(null);
  const [response2, call2] = useAxios({ ...__toggleStatus(toggleStatusId) });
  const [response1, call1] = useAxios({ ...__deleteMessage(messageDeleteId) });

  useEffect(() => {
    call();
  }, []);

  useEffect(() => {
    if (response1 && response1.status) {
      setAlertMessage({ type: 'success', message: response.message });
      call();
    }
  }, [response1]);

  useEffect(() => {
    if (response2 && response2.status) {
      setAlertMessage({ type: 'success', message: response.message });
      call();
    }
  }, [response2]);

  useEffect(() => {
    if (messageDeleteId !== null) {
      call1();
      setTimeout(() => {
        setMessageDeleteId(null);
      }, 100);
    }
  }, [messageDeleteId]);

  useEffect(() => {
    if (toggleStatusId !== null) {
      call2();
      setTimeout(() => {
        setToggleStatusId(null);
      }, 100);
    }
  }, [toggleStatusId]);

  const openAddNewMessage = () => {
    setOpenAddModal(true);
  };

  const OnAddNewMessageModalClose = (data) => {
    if (data && data.success) {
      call();
    }
    setOpenAddModal(false);
  };

  const openEditMessageModal = (data) => {
    setEditData({ ...data });
  };

  const onEditMessageModalClose = (data) => {
    if (data && data.success) {
      call();
    }
    setEditData({});
  };

  const handleDelete = (data) => {
    setDialogData({ ...data });
  };

  const onDeleteConfirm = (data) => {
    setMessageDeleteId(data.id);
  };

  const handleStatusToggle = (message) => {
    setToggleStatusId(message.id);
  };

  return (
    <>
      <Box sx={{ width: '100%', height: '100%' }}>
        <Title variant="h5" content="Messages" gutterBottom />

        {response ? (
          <>
            <MessageList
              response={response}
              addNewMessage={openAddNewMessage}
              handleStatusToggle={handleStatusToggle}
              handleEditMessage={openEditMessageModal}
              handleDelete={(data) => handleDelete(data)}
            />
            <AlertDialog
              dialogClose={(data) => {
                if (data && data.confirm) {
                  onDeleteConfirm(data.item);
                }
                setDialogData({});
              }}
              data={dialogData}
              isOpen={!!Object.keys(dialogData).length}
              cancelText="No, Cancel"
              confirmTextColor="error"
              confirmText="Yes, Delete"
              title="Are you sure you want to delete?"
              description="This process cannot be undo, once you delete it."
            />
          </>
        ) : (
          <Title content="Please wait..." variant="subtitle2" gutterBottom />
        )}
      </Box>
      <AddNewMessage
        isOpen={openAddModal}
        onModalClose={OnAddNewMessageModalClose}
      />
      {Object.keys(editData).length ? (
        <EditMessage
          data={editData}
          isOpen={!!Object.keys(editData).length}
          onModalClose={onEditMessageModalClose}
        />
      ) : (
        ''
      )}
      <AddNewButton title="Add New Message" handleClick={openAddNewMessage} />
    </>
  );
};

export default MessageView;
