const getDefaultMessage = (status) => {
  switch (status) {
    case 200:
      return 'Successful!';
    case 204:
      return 'Successful!';
    case 201:
      return 'Successfully added new resource!';
    case 404:
      return 'Resource not found!!';
    case 400:
      return 'Bad request !';
    case 422:
      return 'The given data is invalid !';
    case 401:
      return 'unauthenticated!';
    case 403:
      return 'Unauthorized action!';
    case 409:
      return 'Cannot perform action due to conflict!';
    case 500:
      return 'Internal server error!';
    case 503:
      return 'Service unavailable';
    default:
      return '';
  }
};

export default getDefaultMessage;
