import { Link } from 'react-router-dom';
import React, { useState, useEffect } from 'react';

import Box from '@mui/material/Box';
import Table from '@mui/material/Table';
import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button';
import TableRow from '@mui/material/TableRow';
import TableCell from '@mui/material/TableCell';
import TableBody from '@mui/material/TableBody';
import TableHead from '@mui/material/TableHead';
import Typography from '@mui/material/Typography';
import TableContainer from '@mui/material/TableContainer';

import TenantActionMenu from './TenantActionMenu';

const TenantList = ({
  response,
  addNewTenant,
  handleEditTenant,
  handleStatusToggle,
  handleApiUpdateTrigger,
}) => {
  const [rows, setRows] = useState([]);

  useEffect(() => {
    if (response && response.status) {
      setRows(response.data.map((each) => each));
    }
  }, [response]);

  if (!rows.length) {
    return (
      <Box
        sx={{ width: '100%', boxShadow: 1, borderRadius: 1, p: 4 }}
        display="flex"
        justifyContent="center"
        flexDirection="column"
        alignItems="center"
      >
        <Typography variant="h6" gutterBottom component="div">
          Tenant not available
        </Typography>
        <Button variant="contained" onClick={addNewTenant}>
          Add New Tenant
        </Button>
      </Box>
    );
  }

  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} stickyHeader>
        <TableHead>
          <TableRow>
            <TableCell>Name</TableCell>
            <TableCell align="right">Code</TableCell>
            <TableCell align="right">Address</TableCell>
            <TableCell align="right">Phone Number</TableCell>
            <TableCell align="right"></TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow
              key={row.name}
              sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
            >
              <TableCell component="th" scope="row">
                <Link to={`${row.id}`}>
                  <Button size="small">{row.name}</Button>
                </Link>
              </TableCell>
              <TableCell align="right">{row.org_code}</TableCell>
              <TableCell align="right">{row.address}</TableCell>
              <TableCell align="right">{row.phone_nos}</TableCell>
              <TableCell align="right">
                <TenantActionMenu
                  data={row}
                  handleEditTenant={handleEditTenant}
                  handleStatusToggle={handleStatusToggle}
                  handleApiUpdateTrigger={handleApiUpdateTrigger}
                />
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
};

export default TenantList;
