import { useReducer } from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';

import PrivateRoute from '../routes/PrivateRoutes';
import { initialAuth, authReducer } from '../reducer/auth';
import { initialAlert, alertReducer } from '../reducer/alert';
import { initialLoading, loadingReducer } from '../reducer/loading';
import { AuthContext, AlertContext, LoadingContext } from '../context/index';

import Login from './auth/Login';
import AlertBox from './common/AlertBox';
import UserView from './views/users/Users';
import LoadingBox from './common/LoadingBox';
import Show from './views/tenants/show/Show';
import BannerView from './views/banner/Banner';
import TenantView from './views/tenants/Tenant';
import MessageView from './views/message/Message';
import SettingView from './views/settings/Setting';

function App() {
  const [auth, authDispatch] = useReducer(authReducer, initialAuth);
  const [alert, alertDispatch] = useReducer(alertReducer, initialAlert);
  const [isLoading, loadingDispatch] = useReducer(
    loadingReducer,
    initialLoading
  );
  return (
    <BrowserRouter>
      <AuthContext.Provider value={[auth, authDispatch]}>
        <AlertContext.Provider value={[alert, alertDispatch]}>
          <LoadingContext.Provider value={[isLoading, loadingDispatch]}>
            <AlertBox />
            <LoadingBox />
            <Routes>
              <Route path="/login" exact element={<Login />} />
              <Route path="/" element={<PrivateRoute />}>
                <Route index element={<h1>Home Page</h1>} />
                <Route path="/tenant">
                  <Route index element={<TenantView />} />
                  <Route path=":id" element={<Show />} />
                </Route>
                <Route path="/user" element={<UserView />} />
                <Route path="/banner" element={<BannerView />} />
                <Route path="/message" element={<MessageView />} />
                <Route path="/setting" element={<SettingView />} />
                <Route path="*" element={<h1>404 Error!!!</h1>} />
              </Route>
            </Routes>
          </LoadingContext.Provider>
        </AlertContext.Provider>
      </AuthContext.Provider>
    </BrowserRouter>
  );
}

export default App;
